<template>
	<view class="value_action">
		<view class="value_con">
			<view class="tit">请您对本次出行进行评价</view>
			<view class="flex-center flex-lr mar-t-30 mar-lr-140">
				<image  class="star" :src="n<postData.quality_grade?'../../static/img/icon_star2.png':'../../static/img/icon_star.png'" v-for="n in 5" :key='n' @click="grade(n)"></image>
			</view>
			<text class="block f-32 color-1 align-c mar-t-30">{{valueDesc[postData.quality_grade-1]}}</text>
			<view class="label_wrap">
				<text class="label" @click="setLabel(el.title)"  :class="postData.label.indexOf(el.title)>-1?'on':''" v-for="el in labelList" :key='el.id'>{{el.title}}</text>
			</view>
			<textarea v-model="postData.text" placeholder="写下您对这次出行的想法吧" />
			<view class="mar-t-30">
				<view class="flex-center radio" @click="chooseAnonymous(postData.is_anonymous)">
					<image class="icon_radio" :src="postData.is_anonymous==2?'../../static/img/icon_radio_check.png':'../../static/img/icon_radio.png'" ></image><text class="f-28 color-1 mar-l-10">匿名评价</text>
				</view>
			</view>
		</view>
		<button type="primary" class="bg-blue mar-lr-30 mar-t-240" @click="submit">发布</button>
	</view>
</template>

<script>
	import OrderApi from '../../apis/order.js'
	export default{
		name:'value_action',
		data(){
			return{
				postData:{
					carpool_id:'',
					quality_grade:5,
					label:[],
					text:'',
					is_anonymous:1,//如果是匿名传2，如果未选可以不传，也可以传1

				},
				valueDesc:['非常差','差','一般','满意','非常满意'],
				labelList:[]
			}
		},
		onLoad(opt) {
			this.postData.carpool_id = opt.id
			this.getLabel()
		},
		methods:{
			getLabel(){
				OrderApi.label().then(res=>{
					this.labelList = res.list
				})
			},
			submit(){
				if(this.postData.text == ''){
					this.$util.showToast('请写下您对这次出行的想法吧')
					return
				}
				OrderApi.value(this.postData).then(res=>{
					this.$util.showToast('评价成功','success')
					setTimeout(()=>{
						uni.switchTab({
							url:'/pages/mine/index'
						})
					},1000)
					
				})
			},
			chooseAnonymous(val){
				this.postData.is_anonymous = val == 2?1:2
			},
			grade(n){
				this.postData.quality_grade = n+1
			},
			setLabel(title){
				let index = this.postData.label.indexOf(title)
				console.log(title,index)
				if(index>-1){
					this.postData.label.splice(index,1)
				}else{
					this.postData.label.push(title)
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.icon_radio{
		width: 38rpx;
		height: 38rpx;
	}
	.value_con{
		background: #fff;
		margin: 20rpx 30rpx;
		border-radius: 8rpx;
		padding: 30rpx 20rpx;
		.tit{
			font-size: 28rpx;
			color: #333;
			display: flex;
			align-items: center;
			&::before{
				content: '';
				display: block;
				width: 8rpx;
				height: 20rpx;
				border-radius: 4rpx;
				background: #0493FF;
				margin-right: 10rpx;
			}
		}
	}
	.mar-lr-140{
		margin: 0 140rpx;
		margin-top: 40rpx;
	}
	.star{
		width: 49rpx;
		height: 49rpx;
	}
	.label_wrap{
		margin-top: 50rpx;
		margin-bottom: 24rpx;
		text{
			line-height: 48rpx;
			font-size: 24rpx;
			color: #666;
			padding: 0 11px;
			margin: 0 11rpx;
			background: #F2F2F2;
			border-radius: 8rpx;
			height: 48rpx;
			display: inline-block;
			margin-bottom: 20rpx;
			&.on{
				background: #0493FF;
				color: #fff;
			}
		}
	}
	textarea{
		background: #F1F1F1;
		border-radius: 8rpx;
		width: 650rpx;
		padding: 30rpx 20rpx;
		box-sizing: border-box;
		font-size: 24rpx;
		height: 240rpx;
	}
	.mar-t-240{
		margin-top: 240rpx;
	}
</style>
